// Functional imports
import cssNameSimplifier from "../../../utility/css-name-simplifier";
import { useEffect, useState, lazy, Suspense } from "react";
import { useNavigate } from "react-router-dom";
import useTabAccessVerifier from "../../../utility/tab-access-verifier";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { useDispatch } from "react-redux";
import isEmpty from "../../../utility/is-empty";
import { AlertSliceActions } from "../../../store/alert-slice";

// Components Imports
import TableView from './TableView';
import { IconButton } from "@mui/material";
import AddNew from "@mui/icons-material/Add";
import Loading from "../../../componets/Loading/Loading";

// Style imports
import importStyles from './ViewAll.module.css';

// Component Imports (Lazy)
const EditSource = lazy(() => import('./EditSource'));


const styles = cssNameSimplifier(importStyles);


function ViewAll(props){
    const dispatch                          = useDispatch();
    const sendRequest                       = useHttpClientWithNavigate();
    const [isAllowedToAccessTab]            = useTabAccessVerifier('view');
    const [sourceData, setSourceData]       = useState([]);
    const [sourceToEdit, setSourceToEdit]   = useState(null);
    const navigate                          = useNavigate();


    if(!isAllowedToAccessTab){
        navigate('/error', { state: { status: 403, msg: 'Your not allowed to access \'View\' tab' } })
    }

    useEffect(() => {
        (async function(){
            try{
                const url = props.baseURL + '/retrive-source-with-subsource';
                const sourceData = await sendRequest({ url });
                setSourceData(sourceData);
            }catch( err ){
                dispatch( AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch booking form data!', title: 'Request Failed!', severity: 'error' }) )
            }
        })();
    }, [dispatch, props.baseURL, sendRequest]);


    function handelSourceEditRequest(id){
        const sourceToEdit = sourceData.filter(source => source.id === id);

        if(isEmpty(sourceData) || sourceToEdit.length <= 0){
            return dispatch( AlertSliceActions.showAlert({ message: 'No source data found!', title: 'Edit Request Failed', severity: 'error' }) );
        }

        setSourceToEdit(sourceToEdit[0]);
    }

    function editSourceRequestsHandeler({ type, action }){
        if(type === 'CLOSE_MODAL'){
            setSourceToEdit(null);
        }
    }

    return (
        <div className={styles.viewContainer}>
            <div className={styles.toolBar}>
                <div className={styles.left}>
                </div>


                <div className={styles.right}>
                    <IconButton>
                        <AddNew />
                    </IconButton>
                </div>
            </div>

            <TableView 
                baseURL={props.baseURL} 
                sourceData={sourceData} 
                handelSourceEditRequest={handelSourceEditRequest}
            />

            <Suspense fallback={<Loading fallback />}>
                {
                    Boolean(sourceToEdit) &&
                    <EditSource 
                        sourceData={sourceToEdit} 
                        requstHandler={editSourceRequestsHandeler}
                    />
                }
            </Suspense>
        </div>
    );
}

export default ViewAll;